:root {
	--background-primary: #ececec;
	--background-secondary: #fafafa;
	--font-color: #909090;
	--max-width: 960px;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

body {
	background: var(--background-secondary);
	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
	.Header {
		.navbar {
			background: var(--background-primary);
			div {
				max-width: var(--max-width);
				margin: 0 auto;
				display: flex;
				justify-content: space-between;
				align-items: center;
				.brand {
					font-size: 20px;
					font-weight: bold;
					color: var(--font-color);
					text-decoration: none;
				}
				.links {
					list-style: none;
					display: flex;
					padding: 10px 0;
					li {
						a {
							padding: 10px;
							text-decoration: none;
							color: black;
						}
						a:hover {
							cursor: pointer;
							background: #fafafa;
							opacity: 0.5;
						}
					}
				}
			}
		}
		h1 {
			padding: 100px 0;
			text-align: center;
		}
	}
}
